@use '../assets/mixins.scss' as *;
.about-products {
  &-inner {
    padding: var(--p160) var(--p291) 0 var(--p291);
    display: flex;
    position: relative;
  }

  &-deco-top {
    position: absolute;
    bottom: 34%;
    left: 26%;
    width: vw(269, 1920);
    height: vw(269, 1920);
    img {
      width: 100%;
      height: 100%;
      transform: rotate(30deg);
    }
  }
  &-deco-bottom {
    position: absolute;
    bottom: 5%;
    left: 5%;
    width: vw(550, 1920);
    height: vw(550, 1920);
    img {
      width: 100%;
      height: 100%;
      transform: rotate(-25deg);
    }
  }
}

.about-clients {
  &-inner {
    padding: var(--p160) var(--p291) 0 var(--p291);
    display: flex;
    position: relative;
  }

  .section-list-left-title {
    width: 40%;
  }
}

.section-list {
  position: relative;
  z-index: 1;
  display: flex;
  &-left {
    padding-right: vw(129, 1920);
    width: 50%;
    display: flex;
    flex-direction: column;
  }

  &-left-title {
    margin-top: var(--g12);
    margin-bottom: var(--g16);
  }
  &-left-descr {
    width: 80%;
  }

  &-right {
    padding-left: vw(129, 1920);
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: var(--p60);
    border-left: 1px dashed var(--deep-grey);
  }

  &-items {
    display: flex;
    flex-direction: column;
    gap: var(--g60);
  }

  &-item {
    display: flex;
    flex-direction: column;
    gap: var(--p24);
  }
}

.about-advantages {
  padding: var(--p160) var(--p291);
  display: flex;
  flex-direction: column;
  gap: var(--g60);
  position: relative;

  &__title-wrapper {
    display: flex;
    flex-direction: column;
    gap: var(--g12);
  }

  &-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }

  &-list__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: var(--g24);
    padding: var(--p60);
    background: var(--white);
    border-radius: 20px;
    transition: all 0.3s ease-in-out;

    &--black {
      background: #1f1e1a;
    }

    &:first-child {
      grid-area: 1 / 1 / 2 / 2;
    }
    &:nth-child(2) {
      grid-area: 1 / 2 / 2 / 3;
    }
    &:nth-child(3) {
      grid-area: 1 / 3 / 3 / 4;
      justify-content: flex-start;
    }
    &:nth-child(4) {
      grid-area: 2 / 1 / 3 / 2;
    }
    &:nth-child(5) {
      grid-area: 2 / 2 / 3 / 3;
    }
  }
}
